//
// SASS Resources
// --------------------------------------------------
// These are shared SASS resources imported in all files via sass-resources-loader.
// Note: Do not import anything that outputs CSS. This file is only used to import variables and mixins.
//

// Make variables and mixins available when using CSS modules.
@import '../../styles/variables';
@import '~bootstrap-sass/assets/stylesheets/bootstrap/_variables';
@import '~bootstrap-sass/assets/stylesheets/bootstrap/_mixins';
@import '../../styles/mixins';

.folder-tree.panel-group {
    max-height: 70%;
    overflow-y: auto;
    .panel {
        border-radius: 0;
        border: 0 solid rgba($black, 0.05);
        border-bottom-width: 1px;

        &-default {
            background: transparent;
        }
        &-heading {
            padding: 0;
            background: transparent;
        }
        &-title > a {
            text-decoration: none;
        }

        .sidebar-nav-link {
            &-content {
                display: flex;
                flex-flow: row nowrap;
                align-items: center;

                i {
                    flex: 0 0 24px;
                }

                > span {
                    flex: 1;
                    min-width: 0;
                    @include text-overflow-ellipsis;
                }
            }
        }

        &-body {
            padding: 0;

            .sidebar-nav-link {
                padding-left: 36px;
            }
        }

        & ~ .panel {
            margin: 0;
        }
    }
}



