//
// SASS Resources
// --------------------------------------------------
// These are shared SASS resources imported in all files via sass-resources-loader.
// Note: Do not import anything that outputs CSS. This file is only used to import variables and mixins.
//

// Make variables and mixins available when using CSS modules.
@import '../../styles/variables';
@import '~bootstrap-sass/assets/stylesheets/bootstrap/_variables';
@import '~bootstrap-sass/assets/stylesheets/bootstrap/_mixins';
@import '../../styles/mixins';

.data-card {
    width: 150px;
    height: 150px;
    margin: 5px;
    background: $white;
    display: flex;
    justify-content: center;
    cursor: pointer;
    flex-direction: column;
    color: $gray;

    &-title {
        font-size: $font-size-h3;
        text-align: center;
        white-space: normal;
        word-break: break-all;
    }
    &:hover {
        cursor: pointer;
        box-shadow: 0 2px 2px 2px rgba($black, 0.2);
    }
}



